.about {
  background-color: paleturquoise;
  height: 100%;
  justify-content: left;
  align-items: center;
  display: flex;
}

#about-card {
  width: 90%;
  padding: 10px;
  margin: 2%;
  height: 33%;
}

img {
  width: 100%;
  height: 100%;
}

@media only screen and (min-width: 768px) {
  #about-card {
    width: 75%;
  }

  img {
    width: 100%;
  }
}

@media only screen and (min-width: 992px) {
  #about-card {
    width: 50%;
  }

  img {
    width: 100%;
  }
}
