.portfolio {
  background-color: rgb(183, 236, 183);
  height: 100%;
  justify-content: left;
  align-items: left;
  display: flex;
  flex-wrap: wrap;
}

.card {
  width: 25%;
  padding: 10px;
  margin: 2%;
  float: left;
}

@media only screen and (max-width: 768px) {
  .card {
    width: 90%;
  }
}

@media only screen and (min-width: 768px) and (max-width: 992px) {
  .card {
    width: 45%;
  }
}
